html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

/** Media Queries **/
h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

body {
  overflow-x: hidden;
}

.header {
  position: relative;
  background-color: #c4c4c4;
}
@media (min-width: 992px) {
  .header {
    background-color: #fff;
  }
}
@media (min-width: 992px) {
  .header__boxBg {
    position: absolute;
    height: 82rem;
    width: 56%;
    max-width: 300rem;
    top: 0;
    right: 0;
    background-color: #c4c4c4;
  }
}
@media (min-width: 3000px) {
  .header__boxBg {
    width: 54%;
  }
}
.header__box {
  margin-top: 5rem;
}
@media (min-width: 992px) {
  .header__box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -3.5rem;
  }
}
.header__pretitle {
  color: #6209DB;
}
.header__pretitle span {
  background-color: #6209DB;
  color: #fff;
  padding: 0.5rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .header__pretitle {
    text-align: center;
  }
}
@media (min-width: 992px) {
  .header__pretitle {
    text-align: left;
  }
}
.header__title {
  font-weight: 700;
  margin-top: 3rem;
  font-size: 6rem;
  text-align: center;
}
.header__title span {
  color: #6209DB;
}
@media (min-width: 992px) {
  .header__title {
    text-align: left;
  }
}
.header__parragraph {
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .header__parragraph {
    font-size: 2rem;
  }
}
@media (min-width: 992px) {
  .header__parragraph {
    text-align: left;
  }
}
.header__btnBox {
  text-align: center;
  margin-bottom: 5rem;
}
@media (min-width: 992px) {
  .header__btnBox {
    text-align: left;
  }
}
.header__btn {
  background-color: #6209DB;
  padding: 2rem 1rem;
  border-radius: 1rem;
  color: #fff;
}
.header__store {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header__store {
    flex-direction: row;
  }
}
@media (min-width: 992px) {
  .header__store {
    justify-content: space-between;
  }
}
.header__apple-btn {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.header__apple-text {
  line-height: 1.5;
}
.header__apple-text span {
  font-size: 2rem;
  font-weight: 700;
}
.header__play-btn {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.header__play-text span {
  font-size: 2rem;
  font-weight: 700;
}
.header__img {
  z-index: 1;
}
.header__imagen {
  margin: 0 auto;
}

.bx {
  font-size: 5rem;
}

.navegacion {
  text-align: center;
  padding-top: 5rem;
}
@media (min-width: 992px) {
  .navegacion {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.navegacion__items {
  margin-top: 2rem;
}
@media (min-width: 992px) {
  .navegacion__items {
    display: flex;
    align-items: center;
    margin-top: 0;
    width: 55%;
  }
}
.navegacion__item {
  z-index: 2;
  margin-top: 1rem;
  font-weight: 600;
  cursor: pointer;
}
@media (min-width: 992px) {
  .navegacion__item {
    margin-top: 0;
    margin-left: 2rem;
  }
  .navegacion__item:first-child {
    margin-left: 0;
  }
}
.navegacion__item--white {
  z-index: 2;
  margin: 0 auto;
  font-weight: 600;
  background-color: #fff;
  padding: 1rem;
  margin-top: 2rem;
  border-radius: 0.5rem;
  cursor: pointer;
}
@media (min-width: 450px) {
  .navegacion__item--white {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .navegacion__item--white {
    width: 40%;
  }
}
@media (min-width: 992px) {
  .navegacion__item--white {
    margin: 0 0 0 2rem;
    margin-left: 2rem;
    transition-property: all;
    transition-duration: 0.3s;
  }
  .navegacion__item--white:hover {
    transform: scale(1.1);
  }
}
.navegacion__item--purple {
  z-index: 2;
  margin-top: 2rem;
  font-weight: 500;
  background-color: #6209DB;
  padding: 1.5rem;
  border-radius: 0.5rem;
  color: #fff;
  cursor: pointer;
}
@media (min-width: 450px) {
  .navegacion__item--purple {
    width: 50%;
    margin: 1.5rem auto 0 auto;
  }
}
@media (min-width: 768px) {
  .navegacion__item--purple {
    width: 40%;
    margin: 1.5rem auto 0 auto;
  }
}
@media (min-width: 992px) {
  .navegacion__item--purple {
    margin-top: 0;
    margin-left: 2rem;
    transition-property: all;
    transition-duration: 0.3s;
  }
  .navegacion__item--purple:hover {
    transform: scale(1.1);
  }
}
.navegacion__title {
  font-weight: 600;
}

.section1 {
  margin-top: 5rem;
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 992px) {
  .section1 {
    margin-top: 15rem;
    display: grid;
    gap: 10rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.section1__title {
  font-size: 5rem;
  font-weight: 700;
  text-align: left;
}
.section1__title span {
  color: #6209DB;
}
@media (min-width: 768px) {
  .section1__title {
    text-align: center;
  }
}
@media (min-width: 992px) {
  .section1__title {
    text-align: left;
    margin-bottom: 5rem;
  }
}
.section1__box {
  margin-top: 5rem;
}
.section1__box:last-child {
  margin-bottom: 5rem;
}
@media (min-width: 992px) {
  .section1__box {
    margin-bottom: 2rem;
  }
}
.section1__number {
  display: none;
  color: #6209DB;
  font-size: 2.4rem;
  font-weight: 600;
}
@media (min-width: 992px) {
  .section1__number {
    display: inline-block;
  }
}
.section1__subtitle {
  font-weight: 700;
}
.section1__subtitle span {
  color: #6209DB;
  font-weight: 600;
}
@media (min-width: 768px) {
  .section1__subtitle {
    text-align: center;
  }
}
@media (min-width: 992px) {
  .section1__subtitle {
    text-align: left;
    margin-bottom: 1rem;
  }
}
@media (min-width: 768px) {
  .section1__parragraph {
    text-align: center;
  }
}
@media (min-width: 992px) {
  .section1__parragraph {
    text-align: left;
    margin: 0;
  }
}
@media (min-width: 768px) {
  .section1__img {
    margin: 0 auto;
  }
}

.section2 {
  margin-top: 5rem;
}
.section2__title {
  text-align: left;
  font-weight: 700;
  font-size: 5rem;
}
.section2__title span {
  color: #6209DB;
}
.section2__parragraph {
  font-size: 2rem;
  text-align: left;
  margin-bottom: 5rem;
}
.section2__cta {
  background-color: #6209DB;
  color: #fff;
  padding: 2rem 2rem;
  border-radius: 5px;
}
.section2__imgBox {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .section2__title {
    text-align: center;
    margin-top: 8rem;
  }
  .section2__parragraph {
    text-align: center;
  }
  .section2__ctaBox {
    text-align: center;
  }
  .section2__imagen {
    margin: 0 auto;
  }
}
@media (min-width: 992px) {
  .section2 {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .section2__title {
    text-align: left;
  }
  .section2__parragraph {
    text-align: left;
  }
  .section2__ctaBox {
    text-align: left;
  }
}

.section3 {
  margin-top: 5rem;
}
@media (min-width: 992px) {
  .section3 {
    margin-top: 12rem;
  }
}
.section3__title {
  text-align: center;
  font-size: 5rem;
  font-weight: 700;
}
@media (min-width: 992px) {
  .section3__title {
    text-align: left;
  }
}
.section3__parragraph {
  text-align: center;
  font-size: 2rem;
}
@media (min-width: 992px) {
  .section3__parragraph {
    text-align: left;
    margin-top: 0;
  }
}

.cards {
  text-align: center;
}
@media (min-width: 992px) {
  .cards {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .cards__card {
    margin-top: 5rem;
  }
}
@media (min-width: 992px) {
  .cards__card {
    margin-top: 0;
  }
}
.cards__imagen {
  margin: 0 auto;
}
.cards__dateBox {
  display: flex;
  justify-content: space-between;
}
.cards__title {
  font-weight: 700;
  text-align: left;
}
.cards__parragraphBox {
  display: flex;
  align-items: center;
}
.cards__parragraph {
  color: #6209DB;
  font-size: 2rem;
  font-weight: 500;
  margin-top: 1rem 0 0 0;
}
@media (min-width: 992px) {
  .cards__parragraph {
    margin: 1rem 0 0 0;
    font-size: 2rem;
  }
}

.bxs-right-arrow-circle {
  color: #6209DB;
  font-size: 3rem;
  margin-left: 1rem;
}
@media (min-width: 992px) {
  .bxs-right-arrow-circle {
    margin-top: 1rem;
  }
}

.section4 {
  margin-top: 5rem;
}
@media (min-width: 992px) {
  .section4 {
    width: 70%;
    margin-top: 12rem;
  }
}
.section4__box {
  background-image: linear-gradient(to left top, #6209db, #5808c5, #4e07af, #45069a, #3b0585);
  color: #fff;
  border-radius: 2rem;
  text-align: center;
  padding: 3rem 0.5rem;
}
@media (min-width: 992px) {
  .section4__box {
    padding: 8rem 20rem;
  }
}
@media (min-width: 1280px) {
  .section4__box {
    padding: 5rem 12rem;
  }
}
.section4__title {
  font-weight: 700;
}
@media (min-width: 992px) {
  .section4__title {
    font-size: 6rem;
  }
}
@media (min-width: 1280px) {
  .section4__title {
    font-size: 5.5rem;
  }
}
.section4__parragraph {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 600;
  margin: 0 0 1.8rem 0;
}

@media (min-width: 768px) {
  .form {
    padding: 0 2rem;
  }
}
.form__box {
  background-color: #fff;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .form__box {
    flex-direction: row;
  }
}
.form__input {
  text-align: center;
  width: 100%;
  border: none;
  margin: 1rem 0 1rem 0;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .form__input {
    grid-column: 1/4;
    width: 80%;
  }
}
@media (min-width: 992px) {
  .form__input {
    font-size: 2rem;
    margin-left: 2rem;
  }
}
.form__submit {
  border: none;
  background-color: #6209DB;
  color: #fff;
  padding: 1.2rem;
  border-radius: 1rem;
  margin: 1rem 0;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form__submit {
    margin-right: 5rem;
  }
}
@media (min-width: 992px) {
  .form__submit {
    padding: 1.5;
  }
}

.footer {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .footer {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 992px) {
  .footer {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__col {
  display: flex;
  flex-direction: column;
}
.footer__colTitle {
  font-size: 2.6rem;
}
.footer__colCta {
  margin-bottom: 2.6rem;
  font-size: 2rem;
  font-weight: 400;
}
.footer__colCta:last-child {
  margin-bottom: 0;
}
.footer__colCta-icons {
  margin: 0 0 2.6rem 0;
}
.footer__icons {
  display: flex;
  align-items: center;
  justify-content: left;
}
.footer__icon {
  margin-left: 3.6rem;
}
.footer__icon:first-child {
  margin-left: 0;
}

.copy {
  margin-top: 3rem;
}
@media (min-width: 992px) {
  .copy {
    margin-top: 8rem;
  }
}
.copy__line {
  width: 100%;
  height: 0.1rem;
  background-color: #000;
}
@media (min-width: 992px) {
  .copy__box {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
  }
}

.box {
  transform: translateX(400%);
  transition: transform 0.8s ease;
}
.box:nth-of-type(even) {
  transform: translateX(-400%);
}
.box.show {
  transform: translateX(0);
}